import './custom.scss';

$(() => {
	let base = "https://learndottogether.herokuapp.com"
	let loading = 0
	let data = null
    $.get(base + "/api/posts/latest", (response) => {
        loading++
        data = response;
    });
	let inter = setInterval((param = data) => {
		if (data !== null) {
            if (data.title) {
                $("#post-title").html(data.title)
            }
            if (data.body) {
                let postBody = data.body.replace('src="', 'src="'+base+'/')
                $("#post-body").html(postBody)
            }
			clearInterval(inter);
		}
	}, 30);
})